// stream  video 
const createVideoElement = async (stream: MediaStream) => {
  const videoEl = document.createElement('video')

  const waitPlayingPromise = new Promise((resolve, reject) => {
    videoEl.addEventListener('playing', resolve, { once: true })
    videoEl.addEventListener('error', reject, { once: true })
  })

  const waitLoadedMetadataPromise = new Promise((resolve, reject) => {
    videoEl.addEventListener('loadedmetadata', resolve, { once: true })
    videoEl.addEventListener('error', reject, { once: true })
  })

  Object.assign(videoEl.style, {
    position: 'fixed',
    width: '1px',
    height: '1px',
    opacity: 0,
  })

  Object.assign(videoEl, {
    muted: true,
    autoplay: true,
    playsInline: true,
    srcObject: stream,
  })

  document.body.appendChild(videoEl)

  await Promise.all([waitPlayingPromise, waitLoadedMetadataPromise])

  return videoEl
}

export default createVideoElement
