import React from 'react'
import Video from '@/components/common/Video'
import styles from './CameraView.css'

type Props = {
  srcObject?: MediaStream
  imageBlob?: unknown
}

const CameraView: React.FC<Props> = ({ srcObject, imageBlob }) => {
  if (imageBlob) {
    return <img src={URL.createObjectURL(imageBlob)} className={styles.base} />
  }

  return (
    <Video
      muted
      autoPlay
      playsInline
      srcObject={srcObject}
      className={styles.base}
    />
  )
}

export default CameraView
