import React from 'react'
import Layout from '@/components/common/Layout'
import CameraView from '@/components/camera/CameraView'
import CameraController from '@/components/camera/CameraController'
import captureImage from '@/helpers/captureImage'

const CameraPage = () => {
  const [stream, setStream] = React.useState<MediaStream>()
  const [imageBlob, setImageBlob] = React.useState<unknown>()

  React.useEffect(() => {
    updateStream()
  }, [])

  // 
  const updateStream = async () => {
    const stream = await navigator.mediaDevices.getUserMedia({
      video: {
        aspectRatio: { ideal: 16 / 9 },
        facingMode: { ideal: 'environment' }, // environment: , user: 
      },
    })

    setStream(stream)
  }

  const handleClickShutter = async () => {
    if (!stream) return

    const blob = await captureImage(stream)
    setImageBlob(blob)
  }

  return (
    <Layout>
      <CameraView srcObject={stream} imageBlob={imageBlob} />
      <CameraController onClickShutter={handleClickShutter} />
    </Layout>
  )
}

export default CameraPage
